@media (max-width:768px){

    .navl h3,.navr h3{
        display: none;
        font:400 1.6vmax var(--font1) ;
    }

    .navbar span{
        display: initial;
        /* position: absolute; */
      }
.tapmenu{
    width: 100vw;
    height: 30vh;
    background-color: rgb(213, 224, 221);
    position: absolute;
    margin-top:9vh ;
    padding-top: 20vh 2vw 2vh 2vw;
    z-index: 50;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    display: none;
}

.tapmenu div{
    display: flex;
    width: 100%;
    align-items: center;
    /* border: 1px solid; */
    justify-content: center;
    margin: 2vh 0;
    box-shadow: 1px 1px 5px;
font:400 2vmax var(--font1) ;
flex:1
/* margin: 2vh 0; */
}
    @keyframes animation {
        0%   {transform: translateX(0vw);}
        20%   {transform: translateX(0vw);}
        25%  {transform: translateX(-100vw);}
        45%  {transform: translateX(-100vw);}
        50%  {transform: translateX(-200vw);}
        75%  {transform: translateX(-200vw);}
        80% {transform: translateX(-300vw);}
        100% {transform: translateX(-300vw);}
      }
.slide{
    width: 100vw;
}
.slide-wrapper{
    width: 100%;  
 }

 .slide h3{
    font: 400 2vmax var(--font1);
  }
